import React, { useState } from "react";
import BasicNav from "../components/common/navigation/BasicNav";

import Button from "@material-ui/core/Button";
import Container from "@material-ui/core/Container";
import Card from "@material-ui/core/Card";
import CardActions from "@material-ui/core/CardActions";
import CardContent from "@material-ui/core/CardContent";
import Typography from "@material-ui/core/Typography";
import Grid from "@material-ui/core/Grid";

const Dashboard = (props) => {
	const [taggedComments, setTaggedComments] = useState(4);
	const [tasks, setTasks] = useState(9);

	const data = require("../utils/data.js").default;

	return (
		<React.Fragment>
			<BasicNav taggedComments={taggedComments} tasks={tasks} />
			<Container maxWidth="lg">
				<br />
				<Typography variant="h4">Your Dashboard</Typography>
				<br />
				<Typography variant="h5">Projects</Typography>
				<br />
				<Grid container spacing={4} justify="center">
					{data.projects.map((project) => {
						return (
							<Grid item xs={12} sm={6} key={project.id}>
								<Card>
									<CardContent>
										<Typography variant="h5" component="h2">
											{project.title}
										</Typography>
										<Typography color="textSecondary">
											{project.role}
										</Typography>
										<Typography
											variant="body2"
											component="p"
										>
											Tasks: {project.tasks.length}
										</Typography>
										<Typography
											variant="body2"
											component="p"
										>
											Team Size: {project.team.length}
										</Typography>
										<Typography
											variant="body2"
											component="p"
										>
											{project.description}
										</Typography>
									</CardContent>
									<CardActions>
										<Button size="small">
											Open {project.title}
										</Button>
									</CardActions>
								</Card>
							</Grid>
						);
					})}
				</Grid>
				<br />
				<br />
			</Container>
		</React.Fragment>
	);
};

export default Dashboard;



