import React, { useState } from "react";
import PropTypes from "prop-types";

//Material-UI Components
import AppBar from "@material-ui/core/AppBar";
import Toolbar from "@material-ui/core/Toolbar";
import IconButton from "@material-ui/core/IconButton";
import Typography from "@material-ui/core/Typography";
import Badge from "@material-ui/core/Badge";

//Material-UI Icons
import MenuIcon from "@material-ui/icons/Menu";
import AccountCircleIcon from "@material-ui/icons/AccountCircle";
import MoreVertIcon from "@material-ui/icons/MoreVert";
import MailIcon from "@material-ui/icons/Mail";
import AssignmentIcon from "@material-ui/icons/Assignment";

//Components
import MobileMenu from "./basic-nav-components/MobileMenu";
import AccountMenu from "./basic-nav-components/AccountMenu";
import DrawerMenu from "./basic-nav-components/DrawerMenu";

import BasicNavStyles from "./basic-nav-components/styles";

const BasicNav = (props) => {
	const classes = BasicNavStyles();

	const [profileMenuAnchorElement, setProfileMenuAnchorElement] = useState(
		null
	);
	const [mobileMenuAnchorElement, setMobileMenuAnchorElement] = useState(
		null
	);
	const [isDrawerOpen, setIsDrawerOpen] = useState(false);

	const isProfileMenuOpen = Boolean(profileMenuAnchorElement);
	const isMobileMenuOpen = Boolean(mobileMenuAnchorElement);

	const handleProfileMenuOpen = (event) =>
		setProfileMenuAnchorElement(event.currentTarget);

	const handleProfileMenuClose = () => {
		setProfileMenuAnchorElement(null);
		handleMobileMenuClose();
	};

	const handleMobileMenuOpen = (event) =>
		setMobileMenuAnchorElement(event.currentTarget);

	const handleMobileMenuClose = () => setMobileMenuAnchorElement(null);

	const handleDrawerOpen = () => setIsDrawerOpen(true);
	const handleDrawerClose = () => setIsDrawerOpen(false);

	const icons = [
		<IconButton aria-label="show 4 new comments" color="inherit">
			<Badge badgeContent={props.taggedComments} color="secondary">
				<MailIcon />
			</Badge>
		</IconButton>,
		<IconButton aria-label="show 11 new tasks" color="inherit">
			<Badge badgeContent={props.tasks} color="secondary">
				<AssignmentIcon />
			</Badge>
		</IconButton>,
		<IconButton
			aria-label="account of current user"
			aria-controls="primary-search-account-menu"
			aria-haspopup="true"
			color="inherit"
			onClick={handleProfileMenuOpen}
		>
			<AccountCircleIcon />
		</IconButton>,
	];

	const mobileMenuId = "primary-search-account-menu-mobile";

	return (
		<div className={classes.grow}>
			<AppBar position="static">
				<Toolbar>
					<IconButton
						edge="start"
						className={classes.menuButton}
						color="inherit"
						aria-label="open drawer"
						onClick={handleDrawerOpen}
					>
						<MenuIcon />
					</IconButton>
					<Typography className={classes.title} variant="h6" noWrap>
						Mantis
					</Typography>
					<div className={classes.grow} />
					<div className={classes.sectionDesktop}>
						{icons[0]}
						{icons[1]}
						{icons[2]}
					</div>
					<div className={classes.sectionMobile}>
						<IconButton
							aria-label="show more"
							aria-controls={mobileMenuId}
							aria-haspopup="true"
							onClick={handleMobileMenuOpen}
							color="inherit"
						>
							<MoreVertIcon />
						</IconButton>
					</div>
				</Toolbar>
			</AppBar>

			<MobileMenu
				isOpen={isMobileMenuOpen}
				anchorElement={mobileMenuAnchorElement}
				handlerMenuClose={handleMobileMenuClose}
				icons={icons}
				id={mobileMenuId}
				handleProfileClick={handleProfileMenuOpen}
			/>
			<AccountMenu
				isOpen={isProfileMenuOpen}
				anchorElement={profileMenuAnchorElement}
				handleClose={handleProfileMenuClose}
			/>

			<DrawerMenu isOpen={isDrawerOpen} handleClose={handleDrawerClose} />
		</div>
	);
};

BasicNav.propTypes = {
	taggedComments: PropTypes.number,
	notifications: PropTypes.number,
};

export default BasicNav;



